const mapStateToProps = state => {
  return {
    authorized: state.authorized,
    priceDetail: state.planPriceDetail.planPrice,
  }
}
const mapDispatchToProps = dispatch => {
  return {
    PLAN__DETAILS: () => dispatch(PLAN__DETAILS()),
    //UPDATE_PLAN__DETAILS: (planId, history) => dispatch(UPDATE_PLAN__DETAILS(planId,history)),
    getUserInfo: (navigate) => dispatch(getUserInfo(navigate))
    
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(NewUserPricePage));

